*{
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
  background-color: black;
}

input {
  color:gray;
  font-size: 1.2rem;
  width:100%;
}

.card-grid{
  display: flex;
  flex-direction:row;
  flex-wrap:wrap;
}

.card{
  align-content:center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow:hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-left: 12px;
  color:white;  
}

.card p {
  padding: 5px;
  text-align: center;
}


.card img{
  max-height: 170px;
  width: 100%;
  height: 100%;
}

.title{
  text-align: center;
  font-size: 60px;
  color:#71F8C9 ;
}

h3{
  text-align: center;
  font-size: 40px;
  color:white;
}

.division{
  padding: 150px;
 }

 .loading{
   color:white;
   font-size: 30px;
 }

 .card:hover{
  border: 3px solid white;
  width: 300px;
  
 }